import React, { useEffect, useState } from "react";
import { Button, Form, Container, Row, Col, ListGroup } from "react-bootstrap";
import { Chart, registerables } from "chart.js";
import { toast } from "react-toastify";
import { Bar } from "react-chartjs-2";
Chart.register(...registerables);
function makeid(length) {
  var result = '';
  var characters =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}
const WorkAnalysis = () => {
  console.log("app started");
  const d = new Date();
  let m = d.getMonth() + 1;
  let date = d.getDate();
  if (m < 10) {
    m = "0" + m;
  }
  if (date < 10) {
    date = "0" + date;
  }
  let dateval = d.getFullYear() + "-" + m + "-" + date;
  // console.log("dateval", dateval);
  let mytime = d.getHours() + ":" + d.getMinutes();
  // console.log("mytime", mytime);
  const [workAnalysis, setWorkAnalysis] = useState({
    Date: dateval,
    time: mytime,
    work: "",
    workType: "office work",
  });
  const [workAnalysisList, setWorkAnalysisList] = useState([]);
  console.log("workAnalysisList:", workAnalysisList);
  const deleteHandler = (id) => {
    setWorkAnalysisList((Prev) => Prev.filter((item) => item.id !== id));

    console.log(id);
  };
  const addReport = (e) => {
    e.preventDefault();
    // console.log("submit handler", workAnalysis);
    setWorkAnalysisList([...workAnalysisList, workAnalysis]);
    setWorkAnalysis({
      Date: dateval,
      time: mytime,
      work: "",
      workType: "office work",
      id: makeid(5),
    });
  };

  const [arr, setArr] = useState([]);

  const dataSet = {
    labels: [
      "office work",
      "extra office work",
      "All Work",
      "apartform office work",
    ],
    datasets: [
      {
        label: "My First Dataset",
        data: arr,
        backgroundColor: [
          "rgba(255, 99, 132, 0.2)",
          "rgba(255, 159, 64, 0.2)",
          "rgba(255, 205, 86, 0.2)",
          "rgba(75, 192, 192, 0.2)",
          "rgba(54, 162, 235, 0.2)",
          "rgba(153, 102, 255, 0.2)",
          "rgba(201, 203, 207, 0.2)",
        ],
        borderColor: [
          "rgb(255, 99, 132)",
          "rgb(255, 159, 64)",
          "rgb(255, 205, 86)",
          "rgb(75, 192, 192)",
          "rgb(54, 162, 235)",
          "rgb(153, 102, 255)",
          "rgb(201, 203, 207)",
        ],
        borderWidth: 1,
      },
    ],
  };

  useEffect(() => {
    let work = 0,
      extra = 0,
      combine = 0,
      apartWork = 0;
    workAnalysisList.filter((item) => {
      if (item.workType === "office work") {
        work += 10;
      } else if (item.workType === "extra office work") {
        extra += 15;
      } else if (item.workType === "apartform office work") {
        apartWork -= 5;
      }

      combine = work + extra;
      setArr((prev) => [work, extra, combine, apartWork]);
    });

    if (arr[2] >= 120) {
      toast.warn("spent with family");
    } else if (arr[2] >= 100) {
      toast.success("doing good");
    } else if (arr[2] < 99) {
      toast.error("doing bad");
    }
  }, [workAnalysisList]);

  console.log("ff");

  console.log("app ented");
  return (
    <div>
      <Container>
        <h1 className="wrk-analysis-title">Today Work Analysis Report</h1>
        <Form onSubmit={addReport}>
          <div className="wrk_analysis">
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Enter the today Work</Form.Label>
              <Form.Control
                as="textarea"
                type="text"
                value={workAnalysis.work}
                placeholder="Write something..."
                onChange={(e) =>
                  setWorkAnalysis({ ...workAnalysis, work: e.target.value })
                }
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Date</Form.Label>
              <Form.Control
                defaultValue={dateval}
                type="date"
                placeholder="Password"
                onChange={(e) =>
                  setWorkAnalysis({ ...workAnalysis, Date: e.target.value })
                }
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>time</Form.Label>
              <Form.Control
                type="time"
                placeholder="Password"
                defaultValue={mytime}
                onChange={(e) =>
                  setWorkAnalysis({ ...workAnalysis, time: e.target.value })
                }
              />
            </Form.Group>
            <Form.Label>Tag</Form.Label>
            <Form.Select
              aria-label="Default select example"
              onChange={(e) =>
                setWorkAnalysis({ ...workAnalysis, workType: e.target.value })
              }
            >
              <option value="office work">office work</option>
              <option value="extra office work">extra office work</option>
              <option value="apartform office work">
                apartform office work
              </option>
            </Form.Select>
          </div>

          <br></br>

          <Button className="wrksubmitbtn" variant="primary" type="submit">
            Submit
          </Button>
        </Form>
        <div className="bar-Chart">
          <Bar data={dataSet} />
        </div>
        <div className="wrk-analysis-list">
          {" "}
          {workAnalysisList.map((item, index) => (
            <Container>
              <Row>
                <ListGroup key={index}>
                  <ListGroup.Item>Work:{item.work}</ListGroup.Item>
                  <ListGroup.Item>Work Type:{item.workType}</ListGroup.Item>
                  <ListGroup.Item>Date:{item.Date}</ListGroup.Item>
                  <ListGroup.Item>Time:{item.time}</ListGroup.Item>
                  <Button className="deletehandle_wrk" onClick={() => deleteHandler(item.id)}>Delete</Button>
                </ListGroup>
              </Row>
            </Container>
          ))}
        </div>
      </Container>
      <div></div>
    </div>
  );
};

export default WorkAnalysis;
