import React, { useState } from "react";
import { Button, Form, Container, Row, Col ,Alert} from "react-bootstrap";
const LoanCalc = () => {
  const [loanAmount, setLoanAmount] = useState();
  const [interestRate, setInterestRate] = useState();
  const [numMonths, setNumMonth] = useState();
  const [monthlyPayment, setMonthlyPayment] = useState();
  const resetValues = () => {
    setLoanAmount("");
    setInterestRate("");
    setNumMonth("");
    setMonthlyPayment("");
  };
  const calculate = (e) => {
    console.log("button");
    e.preventDefault();
    const formValid =
      +loanAmount >= 0 &&
      0 <= +interestRate &&
      +interestRate <= 100 &&
      +numMonths > 0;
    if (!formValid) {
      return;
    }
    setMonthlyPayment((+loanAmount * (1 + +interestRate / 100)) / +numMonths);
  };
  return (
    <div className="loancalc">
      <h1>LOAN EMI CALCULATOR</h1>
      <form onSubmit={calculate}>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Loan Amount</Form.Label>
          <Form.Control
            type="number"
            value={loanAmount}
            onChange={(e) => setLoanAmount(e.target.value)}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>interest rate</Form.Label>
          <Form.Control
            type="number"
            value={interestRate}
            onChange={(e) => setInterestRate(e.target.value)}
          />
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>number of months to pay off loan</Form.Label>
          <Form.Control
            type="number"
            value={numMonths}
            onChange={(e) => setNumMonth(e.target.value)}
          />
        </Form.Group>
        <Button className="apply_loan_btn" variant="primary" type="submit">
          calculate monthly payment
        </Button>{" "}
        &nbsp;&nbsp;
        <Button
          variant="primary"
          className="apply_reset"
          type="submit"
          onClick={resetValues}
        >
          Reset
        </Button>
      </form>
      {monthlyPayment && (
        <Alert variant="success" className="alert_calc_ans">
          <Alert.Heading>
            monthly payment: {monthlyPayment.toFixed(2)}
          </Alert.Heading>
        </Alert>
      )}
    </div>
  );
};

export default LoanCalc;
