import React, { useState } from "react";
import { toast } from "react-toastify";
import { Button, Form, Container, Row, Col, ListGroup } from "react-bootstrap";
function makeid(length) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}
const ApplyLeave = () => {
  const [leave, setLeave] = useState({
    leaveType: "",
    leaveReason: "",
    leaveFrom: "",
    leaveTo: "",
  });
  const [leaveList, setLeaveList] = useState([]);
  const deleteHandler = (id) => {
    setLeaveList((Prev) => Prev.filter((item) => item.id !== id));
    console.log(id);
    toast.warn ("Leave cancelled successfully");
  };
  const submitHandler = (e) => {
    e.preventDefault();
    // console.log("submit handler", workAnalysis);
    setLeaveList([...leaveList, leave]);
    toast.success("Leave applied successfully");
    setLeave({
      leaveType: "",
      leaveReason: "",
      leaveFrom: "",
      leaveTo: "",
      id: makeid(5),
    });
  };
console.log("leave",leaveList.length)
  return (
    <div>
      <Form onSubmit={submitHandler} className='apply_leave_form'>
          <h1 className="leave_title">APPLY LEAVE</h1>
      <Form.Label>Select  the Type </Form.Label>
        <Form.Select
          aria-label="Default select example"
          onChange={(e) => setLeave({ ...leave, leaveType: e.target.value })}
        >
          <option disabled>Select the Reason</option>
          <option value="Sick Leave">Sick Leave</option>
          <option value="Casual Leave">Casual Leave</option>
          <option value="Privilage Leave">Privilage Leave</option>
        </Form.Select>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>Enter the Reason </Form.Label>
          <Form.Control
            type="text"
            placeholder="Enter reason"
            onChange={(e) =>
              setLeave({ ...leave, leaveReason: e.target.value })
            }
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>From Date</Form.Label>
          <Form.Control
            type="date"
            placeholder="Enter reason"
            onChange={(e) => setLeave({ ...leave, leaveFrom: e.target.value })}
          />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicEmail">
          <Form.Label>To Date</Form.Label>
          <Form.Control
            type="date"
            placeholder="Enter reason"
            onChange={(e) => setLeave({ ...leave, leaveTo: e.target.value })}
          />
        </Form.Group>

        <Button  className="apply_leave_btn" variant="primary" type="submit">
          Submit
        </Button>
      </Form>
    <Container >
        <Row>
          <Col>
          {leaveList.length > 0 ?
      (
            <ListGroup className="leave_list">
            <h1 className="leave_applied">LEAVE APPLIED</h1>
              {leaveList.map((item) => (
                <ListGroup.Item>
                  <Row>
                    <Col>
                      <h5>Leave Type: {item.leaveType}</h5>
                    </Col>
                    <Col>
                      <h5>Leave Reason: {item.leaveReason}</h5>
                    </Col>
                    <Col>
                      <h5>From Date: {item.leaveFrom}</h5>
                    </Col>
                    <Col>
                      <h5>To Date: {item.leaveTo}</h5>
                    </Col>
                    <Col>
                      <Button
                        variant="danger"
                        onClick={() => deleteHandler(item.id)}

                      >
                        Cancel Leave
                      </Button>
                    </Col>
                  </Row>
                </ListGroup.Item>
              ))}
            </ListGroup>) : <h1 className="no_leave_list">No Leave Applied</h1>}
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default ApplyLeave;
